  function getQuincenaLabel(fecha) {
    const startOfMonth = dayjs(fecha).startOf("month");
    const middleOfMonth = dayjs(fecha).date(15);
    const startDate =
      fecha.isSame(middleOfMonth) || fecha.isAfter(middleOfMonth)
        ? middleOfMonth.add(1, "day")
        : startOfMonth;
    const endDate = startDate.clone().add(14, "days");
    const monthName = startDate.format("MMMM");
    const quincenaNumber = startDate.date() === 1 ? "Primera" : "Segunda";
    return `${quincenaNumber} quincena de ${monthName} (${startDate.format(
      "YYYY-MM-DD"
    )} - ${endDate.format("YYYY-MM-DD")})`;
  }

