import React from "react";
import { useContext, useRef } from "react";
import { PlanillaContext } from "../../context/PlanillaContext";
import { Input, Button } from "antd";
import { notificacion } from "../../helpers/mensajes";
import "./style/buscador.css";
import {
  handleDownloadExcel,
  handleDownloadExcel1,
} from "../../helpers/tablaExcel";
import { PDFDownloadLink } from "@react-pdf/renderer";

import Item from "antd/es/list/Item";
import { useState } from "react";
import HojaTareoIndividual from "../../helpers/HojaTareoIndividual";
import { pdf } from "@react-pdf/renderer";
import HojaTareoAsociacion from "../../helpers/HojaTareoAsociacion";
const { Search } = Input;

const Buscador = ({
  abrirModal,
  registrar,
  crear,
  exportar,
  cargar,
  actualizar,
  buscador,
  data1,
  validacion,
  modal,
  setFilterText,
  fecha,
  setModal,
  descargar,
}) => {
  const { setControlAsistencia } = useContext(PlanillaContext);
  const [blob, setBlob] = useState(null);
  const inputFileRef = useRef(null);

  const handleModal = () => {
    abrirModal(true);
  };
  const handleValidacion = () => {
    modal(true);
  };

  const changeHandler = () => {
    inputFileRef.current.click();
  };

  const excelFile = async (e) => {
    let formData = new FormData();
    formData.append("myFile", e.target.files[0]);
    formData.append("fecha", fecha);
    const excel = await fetch(
      `${process.env.REACT_APP_BASE}/asistencia/excel`,
      {
        method: "post",
        body: formData,
      }
    );
    const response = await excel.json();
    if (response) {
      notificacion(response.status, response.msg);
      actualizar();
      setControlAsistencia(false);
      setModal(false);
      inputFileRef.current.value = null;
    }
  };

  const excelFileDownload = async (e) => {
    const result = data1.trabajador_asistencia?.map((dat) => {
      return {
        [dat?.asistencium?.fecha]:
          dat.asistencia === "Permiso"
            ? "P"
            : dat.asistencia === "Asistio"
            ? "X"
            : dat.asistencia === "Falto"
            ? "F"
            : dat.asistencia === "Dia libre"
            ? "DL"
            : dat.asistencia === "Comisión"
            ? "C"
            : "F",
      };
    });

    const newObj = Object.assign({}, ...result);
    const result2 = data1?.trabajador_asistencia?.slice(0, 1).map((dat, i) => {
      return {
        Nro: i + 1,
        Nombre: data1?.nombre,
        Dni: data1?.dni,
        Firma_jefe: data1?.firma_jefe,
        Firma_gerente: data1?.firma_gerente,
        Foto: data1?.foto,
        Celular: data1?.telefono,
        Cargo: data1?.contratos?.puesto,
        Área: data1?.contratos?.area,
        ...newObj,
      };
    });
    if (result2) {
      const pdfBlob = await pdf(HojaTareoIndividual(result2)).toBlob();

      if (pdfBlob) {
        // Crea una URL a partir del blob
        const pdfUrl = URL.createObjectURL(pdfBlob);

        // Crea un enlace para descargar el archivo
        const link = document.createElement("a");
        link.href = pdfUrl;
        link.download = "tareo.pdf";
        document.body.appendChild(link);

        // Simula un click en el enlace para descargar el archivo
        link.click();

        // Elimina el enlace del documento
        document.body.removeChild(link);

        // Libera la URL creada a partir del blob
        URL.revokeObjectURL(pdfUrl);
      }
    }
  };

  const pdfAsociacion = async (e) => {
    if (data1) {
      const pdfBlob = await pdf(
        HojaTareoAsociacion(data1.trabajadores)
      ).toBlob();

      if (pdfBlob) {
        // Crea una URL a partir del blob
        const pdfUrl = URL.createObjectURL(pdfBlob);

        // Crea un enlace para descargar el archivo
        const link = document.createElement("a");
        link.href = pdfUrl;
        link.download = "tareo.pdf";
        document.body.appendChild(link);

        // Simula un click en el enlace para descargar el archivo
        link.click();

        // Elimina el enlace del documento
        document.body.removeChild(link);

        // Libera la URL creada a partir del blob
        URL.revokeObjectURL(pdfUrl);
      }
    }
  };

  return (
    <div
      className="buscador-container"
      style={{
        display: "flex",
        alignItems: "center",
        width: "100%",
      }}
    >
      <input
        type="file"
        ref={inputFileRef}
        onChange={excelFile}
        style={{ display: "none" }}
      />
      {buscador ? (
        <Search
          placeholder="Ingresa una nombre..."
          onChange={(e) => setFilterText(e.target.value)}
          style={{
            width: 300,
          }}
        />
      ) : (
        <div style={{ width: "100%" }}>
          {registrar !== false ? (
            <Button onClick={handleModal}>Registrar</Button>
          ) : (
            ""
          )}
          {exportar !== false && data1?.firma_gerente && data1?.firma_jefe ? (
            <Button onClick={excelFileDownload}>Descargar</Button>
          ) : (
            ""
          )}
          {descargar !== false && data1?.firma_gerente && data1?.firma_jefe ? (
            <Button onClick={pdfAsociacion}>Descargar</Button>
          ) : (
            ""
          )}
          {validacion !== false ? (
            <Button onClick={handleValidacion}>Validación</Button>
          ) : (
            ""
          )}
          {cargar !== false ? (
            <div
              style={{
                width: "100%",
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              <Search
                placeholder="Ingresa una nombre..."
                onChange={(e) => setFilterText(e.target.value)}
                style={{
                  width: 300,
                }}
              />
              <Button style={{ width: "100px" }} onClick={changeHandler}>
                Cargar
              </Button>
            </div>
          ) : (
            ""
          )}
        </div>
      )}
    </div>
  );
};

export default Buscador;
